
* {
  line-height: 1.5;
}
::-moz-selection {
  color: black;
  background-color: #3bfc31;
}
::selection {
  color: black;
  background-color: #3bfc31;
}
.github-enter-active {
  -webkit-transition: all 1.8s ease;
  transition: all 1.8s ease;
}
.github-leave-active {
  -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.github-enter, .github-leave-active {
  -webkit-transform: translateX(-100px);
          transform: translateX(-100px);
  opacity: 0;
}
.website-enter-active {
  -webkit-transition: all 1.8s ease;
  transition: all 1.8s ease;
}
.website-leave-active {
  -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.website-enter, .website-leave-active {
  -webkit-transform: translateX(110px);
          transform: translateX(110px);
  opacity: 0;
}
.email-enter-active {
  -webkit-transition: all 1.8s ease;
  transition: all 1.8s ease;
}
.email-leave-active {
  -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.email-enter, .email-leave-active {
  -webkit-transform: translateY(110px);
          transform: translateY(110px);
  opacity: 0;
}
a {
  text-decoration: none;
  position: relative;
}
a::after {
  position: absolute;
  left: 0;
  bottom: -4px;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #3bfc31;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: .3s linear;
  transition: .3s linear;
}
a:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition: .3s linear;
  transition: .3s linear;
}
a:link {
  color: #3bfc31;
}
a:visited {
  color: #3bfc31;
}
/*a:hover {
  text-decoration: underline;
}*/
#app {
    width: 100%;
}
h1 {
    padding-top: 10px;
    text-align: center;
    -webkit-transition: all .3s;
    transition: all .3s;
}
h1:hover {
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
    -webkit-transition: all .3s;
    transition: all .3s;
}
.next {
    position: fixed;
    font-size: 1.1rem;
    top: 80px;
    right: 40px;
}
@-webkit-keyframes scaleIt {
0% {
      -webkit-transform: scale(1);
              transform: scale(1);
}
33% {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
}
66% {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
}
100% {
      -webkit-transform: scale(1);
              transform: scale(1);
}
}
@keyframes scaleIt {
0% {
      -webkit-transform: scale(1);
              transform: scale(1);
}
33% {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
}
66% {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
}
100% {
      -webkit-transform: scale(1);
              transform: scale(1);
}
}
.go {
    position: fixed;
    bottom: 12%;
    right: 23%;
    text-align: center;
    font-size: 22px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 2px solid #3bfc31;
    display: none;
}
.go-plus {
    -webkit-animation: scaleIt 3s infinite;
            animation: scaleIt 3s infinite;
    display: block;
}
.go i {
    display: inline-block;
    width: 100%;
    height: 100%;
    background: url('./go.svg') no-repeat;
    background-size: contain;
    background-position: 5px -5px;
}
#app p {
    text-indent: 1em;
    overflow: hidden;
    width: 100%;
    word-wrap: break-word;
}
.name, .major, .skills, .intro, .website, .github, .email, .achievements {
    padding-left: 100px;
    padding-top: 10px;
}
@media only screen and (max-width: 768px) {
* {
      font-size: 96%;
}
.name, .major, .skills, .intro, .website, .github, .email, .achievements {
      padding-left: 10px;
      padding-top: 5px;
}
h1 {
      padding-top: 0;
      margin-bottom: 0;
      font-size: 130%;
}
* {
      line-height: 1.3;
}
.go {
      right: 29px;
      bottom: 25px;
}
.next {

      top: 40px;
}
}
div {
    padding-top: 10px;
}



